<template>
  <div>
    <v-card
      class="mt-n12 mx-auto middle_card"
      elevation="0"
      height="250"
      max-width="900"
      min-width="340"
      color="#bfe2f5"
    >
      <div class="middle_menu">
        <div class="middle_menu_up">
          <div class="middle_terapeutas">
            <v-btn outlined color="accent" @click.stop="cargarLanzar(1)"
              >Terapeutas</v-btn
            >
          </div>
          <div class="middle_aranceles">
            <v-btn outlined color="accent" @click.stop="cargarLanzar(2)"
              >Aranceles</v-btn
            >
          </div>
          <div class="middle_formaspago">
            <v-btn outlined color="accent" @click.stop="cargarLanzar(3)"
              >Formas de Pago</v-btn
            >
          </div>
        </div>
        <div class="middle_menu_bottom">
          <div>
            <v-img
              class="img-tarjetas"
              height="100"
              width="300"
              max-height="100"
              max-width="300"
              :src="require('@/assets/img/tarjetas.png')"
            ></v-img>

            <showDialog v-model="dialogData" />
          </div>
        </div>
      </div>
    </v-card>
  </div>
</template>

<script>
export default {
    name: 'MiddleMenu',
    data(){
      return{
          dialogData: {
            titulo: "",
            texto: "",
            preformatted: false,
            active: false
          }
      }
    },
    methods: {
     cargarLanzar(seccion){
            switch(seccion){
              case 1:
              
                this.dialogData.titulo =  "Terapeutas";
                this.dialogData.texto = "Esta es la información de los terapeutas";
                this.dialogData.active = true;
              
              break;
              case 2: 
                
                this.dialogData.titulo = "Aranceles",
                this.dialogData.texto = "Esta es la información de los Aranceles",
                this.dialogData.active = true
              
              break;
              case 3:
    
                this.dialogData.titulo = "Formas de pago";
                this.dialogData.texto = "Efectivo \n Cheque al día \n Tarjetas de crédito y débito \n \n Tarjetas de casas comerciales \n y Cajas de Compensación \n a través de I-Med";
                this.dialogData.active = true;
              
              break;
            }
         } 
     }, 
    components:{
      showDialog: () => import('@/components/base/showDialog'),
    }
}
</script>


<style>

/* ****** Middle menu ****** */
.middle_card{
 padding: 30px 60px 60px 60px !important;
 display: flex;
 flex-direction: column;
 justify-content: center;
 border-radius: 60px !important;
}

.middle_menu{
  display: flex;
  flex-direction: column;
}

.middle_menu_up{
  display: flex;
  justify-content: space-around;
  margin-bottom: 10px;
}

.middle_menu_bottom{
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  background-color: #fff;
  padding: 20px;
}

.middle_menu_up h4{
  color: #36ba7f;
}

.middle_menu_up .v-btn {
  color: #36ba7f !important;
  background-color: transparent !important;
  border: 0px solid #adadad !important;
}

@media all and (max-width: 900px) {
  .middle_card{
    border-radius: 0px !important;
  }
}

@media all and (max-width: 426px) {
  .middle_card{
    padding-left: 20px !important;
    padding-right: 20px !important;
    height: 300px !important;
  }
  .middle_menu_up{
    flex-direction: column;
    align-items: center;
  }
}
/* ****** Middle menu ****** */

</style>